import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import AdvertisementApiList from '../../AdvertisementApiList';
import axios from 'axios';
import ApiHeaderAdvrt from '../../ApiHeaderAdvrt';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',

        border: 'none',
        overflowY: "scroll"
    },
};
Modal.setAppElement('#root');
function AgencyNotification(props) {

    const { api_getAgencyDetails } = AdvertisementApiList()
    const [profileDetail, setprofileDetail] = useState()
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen1, setIsOpen1] = useState(false);
    const openModal = () => setIsOpen(true)
    const closeModal = () => setIsOpen(false)
    const afterOpenModal = () => { }
    const openModal1 = () => setIsOpen1(true)
    const closeModal1 = () => setIsOpen1(false)
    const afterOpenModal1 = () => { }

    const modalAgencyInfo = () => {
        openModal()
    }

    const modalNotification = () => {
        openModal1()
    }


    const [showMessage, setshowMessage] = useState([
        {
            id: "1",
            notification: "Hoarding Has Been Approved",
            hoardingNo: "123456789",
            action: 'pay',
        },
        {
            id: "2",
            notification: "Hoarding Has Been Approved",
            hoardingNo: "3433456789",
            action: 'pay',
        },
        {
            id: "3",
            notification: "Hoarding Has Been Approved",
            hoardingNo: "1344345688",
            action: 'pay',
        },
        {
            id: "4",
            notification: "Hoarding Has Been Approved",
            hoardingNo: "1344345688",
            action: 'pay',
        },

    ])

    ///////////{*** get agency details ***}/////////
    useEffect(() => {
        getAgencyDetails()
    }, [])
    const getAgencyDetails = () => {
        // props.showLoader(true);
        const requestBody = {
            applicationId: props.applicationId,
        }
        axios.post(`${api_getAgencyDetails}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('agency details', response.data.data.data)
                setprofileDetail(response.data.data.data)
                // setTimeout(() => {
                //     props.showLoader(false);
                // }, 500);
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                // setTimeout(() => {
                //     props.showLoader(false);
                // }, 500);

            })
    }

    return (
        <>
            <div className='flex space-x-6 justify-end mr-16'>
                <h1 className='text-center text-lg font-semibold text-gray-500'>{profileDetail?.entity_name}</h1>
                <button className='' onClick={() => modalAgencyInfo()}>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-8 h-8 p-1 rounded-full text-indigo-500 bg-gray-50 darks:bg-pink-900 darks:bg-opacity-60 border border-indigo-500">
                        <path fill-rule="evenodd" d="M8.25 6.75a3.75 3.75 0 117.5 0 3.75 3.75 0 01-7.5 0zM15.75 9.75a3 3 0 116 0 3 3 0 01-6 0zM2.25 9.75a3 3 0 116 0 3 3 0 01-6 0zM6.31 15.117A6.745 6.745 0 0112 12a6.745 6.745 0 016.709 7.498.75.75 0 01-.372.568A12.696 12.696 0 0112 21.75c-2.305 0-4.47-.612-6.337-1.684a.75.75 0 01-.372-.568 6.787 6.787 0 011.019-4.38z" clip-rule="evenodd" />
                        <path d="M5.082 14.254a8.287 8.287 0 00-1.308 5.135 9.687 9.687 0 01-1.764-.44l-.115-.04a.563.563 0 01-.373-.487l-.01-.121a3.75 3.75 0 013.57-4.047zM20.226 19.389a8.287 8.287 0 00-1.308-5.135 3.75 3.75 0 013.57 4.047l-.01.121a.563.563 0 01-.373.486l-.115.04c-.567.2-1.156.349-1.764.441z" />
                    </svg>
                </button>

                <button className='' onClick={() => modalNotification()} >
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-8 h-8 p-1 rounded-full text-red-500 bg-gray-50 darks:bg-pink-900 darks:bg-opacity-60 border border-red-500">
                        <path stroke-linecap="round" stroke-linejoin="round" d="M14.857 17.082a23.848 23.848 0 005.454-1.31A8.967 8.967 0 0118 9.75v-.7V9A6 6 0 006 9v.75a8.967 8.967 0 01-2.312 6.022c1.733.64 3.56 1.085 5.455 1.31m5.714 0a24.255 24.255 0 01-5.714 0m5.714 0a3 3 0 11-5.714 0M3.124 7.5A8.969 8.969 0 015.292 3m13.416 0a8.969 8.969 0 012.168 4.5" />
                    </svg>
                </button>
            </div>

            {/*view application full detail */}
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div class="mx-auto px-0 overflow-y-auto" style={{ 'width': '25vw', 'height': '50%' }}>
                    <div className='h-[44rem] bg-violet-100 border border-violet-500 overflow-y-auto rounded leading-5 '>
                        {/* {tabIndex == 1 && */}
                        <div className={``}>
                            <div>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="h-20 rounded-full bg-gray-200 mx-auto  text-indigo-500 mt-4">
                                    <path fill-rule="evenodd" d="M8.25 6.75a3.75 3.75 0 117.5 0 3.75 3.75 0 01-7.5 0zM15.75 9.75a3 3 0 116 0 3 3 0 01-6 0zM2.25 9.75a3 3 0 116 0 3 3 0 01-6 0zM6.31 15.117A6.745 6.745 0 0112 12a6.745 6.745 0 016.709 7.498.75.75 0 01-.372.568A12.696 12.696 0 0112 21.75c-2.305 0-4.47-.612-6.337-1.684a.75.75 0 01-.372-.568 6.787 6.787 0 011.019-4.38z" clip-rule="evenodd" />
                                    <path d="M5.082 14.254a8.287 8.287 0 00-1.308 5.135 9.687 9.687 0 01-1.764-.44l-.115-.04a.563.563 0 01-.373-.487l-.01-.121a3.75 3.75 0 013.57-4.047zM20.226 19.389a8.287 8.287 0 00-1.308-5.135 3.75 3.75 0 013.57 4.047l-.01.121a.563.563 0 01-.373.486l-.115.04c-.567.2-1.156.349-1.764.441z" />
                                </svg></div>
                            <div><h1 className='text-center font-semibold text-xl'>{profileDetail?.entity_name}</h1></div>
                            <div><h1 className='text-center text-gray-600 font-semibold text-md'>Your License is valid for 5 years <br /> {profileDetail?.approve_date}  to  {profileDetail?.expiry_date}</h1></div>
                            {/* {profileDetail?.warning == 1 && */}
                            {/* <div className='text-center'>
                                <button className=" mt-4 py-1 px-4 text-xs inline-block text-center shadow-lg  rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0 animate-pulse" onClick={() => navigate(`/agency/${profileDetail?.id}/renew`)}>Renew your license</button>
                                <h1 className='text-sm font-bold mt-1'>your license is going to expire on <span className='text-red-500'>{profileDetail?.expiry_date}</span>.To continue with the license Renew It. </h1>
                            </div> */}
                            <div className='text-center'>
                                <button className=" mt-4 py-1 px-4 text-xs inline-block text-center shadow-lg  rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0 animate-pulse" >Renew your license</button>
                                <h1 className='text-sm font-bold mt-1 p-4'>your license is going to expire on <span className='text-red-500'>{profileDetail?.expiry_date}</span>.To continue with the license Renew It. </h1>
                            </div>
                            {/* } */}
                            <div>
                                <table className='border mt-8 overflow-y-auto p-2'>
                                    <thead className='bg-violet-200'>
                                        <tr className='border '>
                                            <td className='border px-12 text-sm'>Members</td>
                                            <td className='border px-12 text-sm'>Mobile No.</td>
                                            <td className='border px-12 text-sm'>Email</td>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {profileDetail?.directors?.map((data) => (
                                            <tr className='text-center text-sm'>
                                                <td className='border'>{data?.director_name}</td>
                                                <td className='border'>{data?.director_mobile}</td>
                                                <td className='border'>{data?.director_email}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        {/* } */}

                    </div>
                </div>
            </Modal>


            {/*if document is BTC then reupload document */}
            <Modal
                isOpen={modalIsOpen1}
                onAfterOpen={afterOpenModal1}
                onRequestClose={closeModal1}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div class="mx-auto px-0 overflow-y-auto" style={{ 'width': '25vw', 'height': '50%' }}>
                    <div className='h-[44rem] bg-violet-100 border border-violet-500 overflow-y-auto rounded leading-5 '>
                        <div className={``}>
                            <div className='flex flex-row justify-center p-2 mt-3 bg-white mb-4'>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-6 h-6 text-red-500 text-center animate-pulse">
                                    <path d="M5.85 3.5a.75.75 0 00-1.117-1 9.719 9.719 0 00-2.348 4.876.75.75 0 001.479.248A8.219 8.219 0 015.85 3.5zM19.267 2.5a.75.75 0 10-1.118 1 8.22 8.22 0 011.987 4.124.75.75 0 001.48-.248A9.72 9.72 0 0019.266 2.5z" />
                                    <path fill-rule="evenodd" d="M12 2.25A6.75 6.75 0 005.25 9v.75a8.217 8.217 0 01-2.119 5.52.75.75 0 00.298 1.206c1.544.57 3.16.99 4.831 1.243a3.75 3.75 0 107.48 0 24.583 24.583 0 004.83-1.244.75.75 0 00.298-1.205 8.217 8.217 0 01-2.118-5.52V9A6.75 6.75 0 0012 2.25zM9.75 18c0-.034 0-.067.002-.1a25.05 25.05 0 004.496 0l.002.1a2.25 2.25 0 11-4.5 0z" clip-rule="evenodd" />
                                </svg>
                                <h1 className='font-BreeSerif text-center text-gray-700'>NOTIFICATION</h1>
                            </div>
                            {showMessage.map((items) => (
                                <div className='flex bg-white shadow-sm p-2 mb-3 '>
                                    <h1 className='text-md text-gray-800 ml-4'>
                                        {items.notification} <br /> <span className='text-sm font-bold'>HOARDING NO. :- {items.hoardingNo}</span>
                                    </h1>
                                    {items.action != null || items.action != undefined || items.action != '' &&
                                        <button type="button" class="text-sm ml-16 mt-2 lg:ml-20 md:ml-20 px-4 inline-block text-center  rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0">{items.action}</button>}
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </Modal>

        </>
    )
}

export default AgencyNotification