import React, { useEffect, useState } from 'react'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import ApiHeaderAdvrt from '../ApiHeaderAdvrt';
import axios from 'axios';
import AdvertisementApiList from '../AdvertisementApiList';
import _ from 'lodash'

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function BarGraphComponent2() {

  const { getAgencyDashboardGraphData } = AdvertisementApiList();
  const [hoardingBarGraph, sethoardingBarGraph] = useState(null)


  ///////////{***  MONTHLY HOARDING APPLIED  ***}/////////
  useEffect(() => {
    fetchMonthlyHoardingData()
  }, [])
  const fetchMonthlyHoardingData = () => {
    const requestBody = {
      deviceId: "graphData"
    }
    axios.post(`${getAgencyDashboardGraphData}`, requestBody, ApiHeaderAdvrt())
      .then(function (response) {
        console.log('fetch hoarding applied monthly', response)
        sethoardingBarGraph(response?.data?.data)
      })
      .catch(function (error) {
        console.log('errorrr.... ', error);
      })
  }

  console.log("bar graph data ", hoardingBarGraph)

  const options = {
    responsive: true,
    animation: {
      duration: 2000 // set the duration of the animation in milliseconds
    },
    plugins: {
      legend: {
        position: 'bottom',
      },
      title: {
        display: true,
        text: 'Monthly Hoarding Applied ',
      },
    },
  };


  // let labels = props?.data?.map((data) => {
  //   return data?.ulb
  // })
  // let collection = props?.data?.map((data) => {
  //   return data?.collection
  // })
  let labels = hoardingBarGraph?.graph?.map((data) => {
    return data?.month
  })
  let totalHoarding = hoardingBarGraph?.graph?.map((data) => {
    return data?.total
  })
  let approveHoarding = hoardingBarGraph?.graph?.map((data) => {
    return data?.approved
  })
  let rejectedHoarding = hoardingBarGraph?.graph?.map((data) => {
    return data?.reject
  })

  const data = {
    labels,
    datasets: [
      {
        label: 'Total',
        data: totalHoarding,
        backgroundColor: ['#6366f1'],
      },
      {
        label: 'Approve',
        data: approveHoarding,
        backgroundColor: ['#22c55e'],
      },
      {
        label: 'Reject',
        data: rejectedHoarding,
        backgroundColor: ['#ef4444'],
      },
    ],
  };
  return (
    <>
      <Bar options={options} data={data} />
    </>
  )
}

export default BarGraphComponent2