import { useState, useEffect } from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import _ from 'lodash'
import AdvertisementApiList from '../AdvertisementApiList';
import ApiHeaderAdvrt from '../ApiHeaderAdvrt';
import axios from 'axios';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);


function LineChartComponent() {

    let secondLable = 'Total Collection'

    const [lineChartData, setlineChartData] = useState(null)

    const { getAgencyDashboardGraphData } = AdvertisementApiList();


    ///////////{***  MONTHLY HOARDING APPLIED  ***}/////////
    useEffect(() => {
        fetchMonthlyHoardingData()
    }, [])
    const fetchMonthlyHoardingData = () => {
        const requestBody = {
            deviceId: "graphData"
        }
        axios.post(`${getAgencyDashboardGraphData}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('fetch payment details', response)
                setlineChartData(response?.data?.data)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }

    console.log("list chart graph data ", lineChartData)
    // useEffect(() => {
    //     setlineChartData({
    //         labels,
    //         datasets: [
    //             {
    //                 label: 'Total Collection',
    //                 data: '12',
    //                 borderColor: '#4BB14F',
    //                 backgroundColor: '#4BB14F',
    //             },
    //         ],
    //     })
    // }, [])
    let labels = lineChartData?.payment?.map((data) => {
        return data?.month
    })

    let collection = lineChartData?.payment?.map((data) => {
        return data?.total
    })

    const data = {
        labels,
        datasets: [
            {
                label: 'Total Collection',
                data: collection,
                borderColor: '#4BB14F',
                backgroundColor: '#4BB14F',
            },
        ],
    };
    const options = {
        responsive: true,
        animation: {
            duration: 2000 // set the duration of the animation in milliseconds
        },
        plugins: {
            legend: {
                position: 'bottom',
            },
            title: {
                display: true,
                text: 'Monthly Payment collection ',
            },
        },
    };

    return (
        <>
            <Line options={options} data={data} />
        </>
    )
}

export default LineChartComponent